import React from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/app/components/ui/card';
import { ScrollArea } from '@/app/components/ui/scroll-area';

const AboutPage = () => {
  return (
    <div className="max-w-4xl mx-auto">
      <h1 className="text-2xl font-bold mb-6">About Parasara Jyothish Bot</h1>
      
      <div className="space-y-6">
        <Card>
          <CardHeader>
            <CardTitle>Overview</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-gray-600">
              Parasara Jyothish Bot is an AI-powered astrological consultation platform 
              that provides personalized insights based on Vedic astrology principles.
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Key Features</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div>
                <h3 className="font-semibold mb-2">Career Insights</h3>
                <p className="text-gray-600">
                  Analysis of 6th and 10th houses for professional guidance and career prospects.
                </p>
              </div>
              <div>
                <h3 className="font-semibold mb-2">Relationship Guidance</h3>
                <p className="text-gray-600">
                  Marriage and relationship predictions based on 5th and 7th house analysis.
                </p>
              </div>
              <div>
                <h3 className="font-semibold mb-2">Financial Predictions</h3>
                <p className="text-gray-600">
                  Money and income insights through analysis of 2nd and 10th houses.
                </p>
              </div>
              <div>
                <h3 className="font-semibold mb-2">Personalized Horoscopes</h3>
                <p className="text-gray-600">
                  Daily, weekly, and yearly predictions tailored to your birth details.
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Technology</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <h3 className="font-semibold mb-2">Frontend</h3>
                <p className="text-gray-600">Next.js</p>
              </div>
              <div>
                <h3 className="font-semibold mb-2">Backend</h3>
                <p className="text-gray-600">Flask</p>
              </div>
              <div>
                <h3 className="font-semibold mb-2">Database</h3>
                <p className="text-gray-600">MongoDB</p>
              </div>
              <div>
                <h3 className="font-semibold mb-2">AI Integration</h3>
                <p className="text-gray-600">ChatGPT API</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default AboutPage;